@import url('https://fonts.googleapis.com/css2?family=JetBrains+Mono:wght@400;500;700&family=Unbounded:wght@300;400;600;700&display=swap');

@tailwind base;
@tailwind components;
@tailwind utilities;

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: 'JetBrains Mono', monospace;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #050505;
  color: #EDEDED;
}

/* Scrollbar styling */
::-webkit-scrollbar {
  width: 6px;
  height: 6px;
}

::-webkit-scrollbar-track {
  background: transparent;
}

::-webkit-scrollbar-thumb {
  background: rgba(0, 240, 255, 0.2);
  border-radius: 3px;
}

::-webkit-scrollbar-thumb:hover {
  background: rgba(0, 240, 255, 0.4);
}

/* Custom utilities */
@layer utilities {
  .font-display {
    font-family: 'Unbounded', sans-serif;
  }
  
  .font-mono {
    font-family: 'JetBrains Mono', monospace;
  }
  
  .text-glow {
    text-shadow: 0 0 10px currentColor, 0 0 20px currentColor;
  }
  
  .text-glow-sm {
    text-shadow: 0 0 5px currentColor;
  }
  
  .border-glow {
    box-shadow: 0 0 10px currentColor, inset 0 0 10px rgba(0, 240, 255, 0.1);
  }
  
  .glass {
    background: rgba(10, 10, 10, 0.8);
    backdrop-filter: blur(12px);
    -webkit-backdrop-filter: blur(12px);
  }
  
  .glass-heavy {
    background: rgba(10, 10, 10, 0.9);
    backdrop-filter: blur(24px);
    -webkit-backdrop-filter: blur(24px);
  }
}

/* Scanlines overlay */
.scanlines::after {
  content: '';
  pointer-events: none;
  position: fixed;
  inset: 0;
  z-index: 9999;
  background: linear-gradient(
    rgba(18, 16, 16, 0) 50%,
    rgba(0, 0, 0, 0.25) 50%
  ), linear-gradient(
    90deg,
    rgba(255, 0, 0, 0.06),
    rgba(0, 255, 0, 0.02),
    rgba(0, 0, 255, 0.06)
  );
  background-size: 100% 2px, 3px 100%;
  opacity: 0.15;
}

/* Grid background */
.grid-bg {
  background-image: 
    linear-gradient(to right, #222 1px, transparent 1px),
    linear-gradient(to bottom, #222 1px, transparent 1px);
  background-size: 4rem 4rem;
  mask-image: radial-gradient(ellipse 80% 50% at 50% 0%, #000 70%, transparent 100%);
}

/* Animated gradient border */
.gradient-border {
  position: relative;
}

.gradient-border::before {
  content: '';
  position: absolute;
  inset: 0;
  padding: 1px;
  background: linear-gradient(135deg, #00F0FF, #39FF14, #BD00FF, #00F0FF);
  background-size: 300% 300%;
  animation: gradient-shift 8s ease infinite;
  -webkit-mask: linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0);
  -webkit-mask-composite: xor;
  mask-composite: exclude;
  border-radius: inherit;
  pointer-events: none;
}

@keyframes gradient-shift {
  0% { background-position: 0% 50%; }
  50% { background-position: 100% 50%; }
  100% { background-position: 0% 50%; }
}

/* Pulse animation */
@keyframes pulse-glow {
  0%, 100% { opacity: 1; }
  50% { opacity: 0.5; }
}

.animate-pulse-glow {
  animation: pulse-glow 2s ease-in-out infinite;
}

/* Breathing animation */
@keyframes breathe {
  0%, 100% { transform: scale(1); opacity: 0.8; }
  50% { transform: scale(1.05); opacity: 1; }
}

.animate-breathe {
  animation: breathe 4s ease-in-out infinite;
}

/* Status indicators */
.status-online {
  background: #39FF14;
  box-shadow: 0 0 10px #39FF14;
}

.status-offline {
  background: #FF003C;
  box-shadow: 0 0 10px #FF003C;
}

.status-warning {
  background: #FFB800;
  box-shadow: 0 0 10px #FFB800;
}

/* Card hover effect */
.card-hover {
  transition: transform 0.2s ease, box-shadow 0.2s ease;
}

.card-hover:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 30px rgba(0, 240, 255, 0.15);
}

/* Tracing beam effect */
.tracing-beam {
  position: relative;
  overflow: hidden;
}

.tracing-beam::after {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 2px;
  background: linear-gradient(90deg, transparent, #00F0FF, transparent);
  animation: trace 3s linear infinite;
}

@keyframes trace {
  0% { left: -100%; }
  100% { left: 100%; }
}

/* Dark theme variables */
@layer base {
  :root {
    --background: 0 0% 2%;
    --foreground: 0 0% 93%;
    --card: 0 0% 4%;
    --card-foreground: 0 0% 93%;
    --popover: 0 0% 4%;
    --popover-foreground: 0 0% 93%;
    --primary: 186 100% 50%;
    --primary-foreground: 0 0% 0%;
    --secondary: 105 100% 55%;
    --secondary-foreground: 0 0% 0%;
    --muted: 0 0% 10%;
    --muted-foreground: 0 0% 53%;
    --accent: 285 100% 50%;
    --accent-foreground: 0 0% 100%;
    --destructive: 348 100% 50%;
    --destructive-foreground: 0 0% 100%;
    --border: 0 0% 13%;
    --input: 0 0% 13%;
    --ring: 186 100% 50%;
    --radius: 0.25rem;
  }
}

@layer base {
  * {
    @apply border-border;
  }
  body {
    @apply bg-background text-foreground;
  }
}
